#albumcontainer{
  overflow:hidden;
  width:100%;
  height: 100%;
}
#album{ 
  height: 100%;
  width: 100%;
  overflow: scroll;
  overflow-x: hidden;
  width: calc(100% + 1vw);
  /* background-color: white; */
}
.item{ 
  height: auto;
  text-align: left;
  padding: 0;
  width: 100%;
  float: right;
  height: 200px;
  position: relative;
}
.item:nth-child(even) {
  /* background-color: red; */
  float: left;
  text-align: right;
}
.image{
  height: 100%;
  float: right;
  right: 0%;
}
.item:nth-child(even) .image{
  float: left;
  left: 0%;
}
.item img{
  height: 100%;
  width: auto;
}
.pButton{
  height: 75%;
  width: 75%;
  background-color: red;
  margin-right: auto;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  margin-left: auto;
}
.item:nth-child(even) .purchase{
  float: left;
  left: 0%;
}
.purchase{
  float: right;
  right: 0%;
  /* position: relative; */
  height: 100%;
  width: 20%;
  background-color: green;
}
body{
  overflow: hidden;
}
.anim{
  -webkit-transition: all .5s;
          transition: all .5s;
}
.address{
  width:100%;
}
